%, -50%) scale3d(1, 1, 1);transform:translate(-50%, -50%) scale3d(1, 1, 1);transition:all 300ms cubic-bezier(0.4, 0, 0.2, 1);opacity:0;z-index:-1}.hours-container .inputGroup label:after{width:32px;height:32px;content:"";border:2px solid #d1d7dc;background-color:#fff;background-image:url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");background-repeat:no-repeat;background-position:2px 3px;border-radius:50%;z-index:2;position:absolute;right:30px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);cursor:pointer;transition:all 200ms ease-in}.hours-container .inputGroup input:checked ~ label{color:#fff}.hours-container .inputGroup input:checked ~ label:before{-webkit-transform:translate(-50%, -50%) scale3d(56, 56, 1);transform:translate(-50%, -50%) scale3d(56, 56, 1);opacity:1}.hours-container .inputGroup input:checked ~ label:after{background-color:#54e0c7;border-color:#54e0c7}.hours-container .inputGroup input{width:32px;height:32px;order:1;z-index:2;position:absolute;right:30px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);cursor:pointer;visibility:hidden}.hours-container .nonAvailable{border:1px solid #ff0000;background-color:rgba(255,0,0,0.1)}.hours-container .nonAvailable label::after{visibility:hidden}

